.gifBlock {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 80px 15% 0 15%;
  margin: 0 auto;
  height: 100vh;
  overflow: auto;
}

.gifBlock::-webkit-scrollbar {
  width: 3px;
  background-color: none;
}
.gifBlock::-webkit-scrollbar-thumb {
  background-color: white;
  border-radius: 9em;
}

.preloader {
  margin: 0 auto;
}

