.gif_item {
  display: flex;
  align-items: center;
  width: 180px;
  /* height: 180px; */
  margin: 0 2px 5px 2px;
  overflow: hidden;
  cursor: pointer;
  transition: all 0.2s;
  position: relative;
}

.gif_item img {
  width: 100%;
}

.gif_action:hover {
  transform: scale(1.2);
  z-index: 10;
}
.gif_action:active {
  opacity: 0.2;
}

.popup {
  display: flex;
  align-items: center;
  background: pink;
  position: absolute;
  padding: 5px 10px;
  width: 80px;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  border-radius: 8px;

  animation: showpopup 0.2s linear;
}

@keyframes showpopup {
  0% {
    transform: translate3d(0px, -100%, 0px);
    opacity: 0;
  }
  100% {
    transform: translate3d(0px, 0px, 0px);
    opacity: 1;
  }
}

@media (max-width: 1015px) {
  .gifBlock {
    justify-content: center;
    padding: 80px 0 0 0;
  }
}

@media (max-width: 414px) {
  .gif_item {
    width: 100px;
    height: 100px;
  }
  .gif_action:hover {
    transform: none;
  }
}

@media (max-width: 320px) {
  .gif_item {
    width: 80px;
    height: 80px;
  }
}
